<template>
    <div class="midden">
        <loading></loading>
    </div>
</template>

<script>
    import loading from '../components/loading.vue'
    export default {
        mounted() {
            var vm = this;
            let timer = setTimeout(function () {
                vm.$router.push('/home');
                clearTimeout(timer)
            },1500)
        },
        components: {
            loading
        }
    }
</script>

<style>
    .midden {
        position: absolute;
        top: 50%;
        width: 100%;
        margin-top: -40px;
    }
</style>


